.buttons-container {
  display: flex;
  justify-content: center;
  align-items: center;
  /* 确保父元素有足够的高度 */
  height: 100vh; /* 例如，使其高度为视口高度 */
}

.square {
  width: 100px;
  height: 100px;
  background-color: #61dafb;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  color: white;
  margin: 10px;
  cursor: pointer;
}
